
import React, { useEffect, useReducer } from "react";
import reducer from "../Store/reducers/reducer";

export default () => {
  const [state, dispatch] = useReducer (reducer, []);

  useEffect(() => {
    localStorage.setItem('token', JSON.stringify(state))
  }, state);

  return(
    <div>
      <h1>second page</h1>
      <div>token: {state}</div>
    </div>
  );
};
